import React, {useState} from 'react'
import './comp.css'
import axios from 'axios'


export default function MainApp(props){

const { loggingIn, loggedIn } = props;
    return (
      <div>
        {(() => {
          if (loggingIn) {
            return <h1 className="words">HELLO</h1>
          } else {
            <h1 className="words" >Please Log In</h1>
          }
        })()}
      </div>
    )
  }




